body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.sourcelist:hover {
  background: #eee;
  padding: 2px;
}
.config {
  width: 100%;
  height: 32px;
  border: 1px dashed;
  text-align: center;
  background: #e8e8e8;
  line-height: 2;
}
.item:hover {
  padding: 2px;
}
.config:hover {
  color: #3db389;
  background: #e8e8e8;
  border-color: #3db389;
}
.dndInstruction {
  font-size: x-large;
  top: 40%;
  position: absolute;
  margin-left: 31%;
}
.droppedItems {
  height: 64vh;
  overflow: auto;
  border: 1px dashed;
  background: #f9f9fb;
}
.demo {
  overflow: auto;
  height: 10px;
  border: 1px solid #d9d9d9;
  background-color: white;
  width: 500px;
  min-height: 37px;
}
.public-DraftStyleDefault-block {
  margin: 0em 0 !important;
}

.email-template {
  min-height: 300px ;
}
.rdw-dropdown-wrapper a span {
  color: #000;
}
.demo-editor {
  height: 200px;
  min-height: 300px;
}

